nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(20, 170, 220);
  padding: 10px 0;
}

nav ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}

nav ul a {
  color: white;
  font-size: 20px;
  font-family: sans-serif;
  font-weight: 300;
  text-decoration: none;
}