body {
  font-family: Arial, sans-serif;
  margin:   0;
  padding: 0;
  box-sizing: border-box;
}

.homecontainer {
  max-width: 100%;
  margin: 0 auto ;
  margin-top: 45px;
  height: 100%;
  
}

.homeformcontainer {
 box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
 background-image: url('hospital\ image.jpg'); /* Add your background image */
 background-size: cover;
 padding: 30px;
 border-radius: 10px;
 width: 60%;
 margin:0 auto;
 display: flex; /* Add this line to use flexbox */
 flex-direction: column; /* Make it a column layout */
 align-items: center; /* Center items horizontally */
}

.homeformcontainer input {
 border: 1px solid rgba(0, 0, 0, 0.5);
 outline: none;
 width: 70%; /* Adjusted width for better visibility */
 margin: 10px 0;
 padding: 15px;
 background: rgba(255, 255, 255, 0.8);
 border-radius: 8px;
 font-size: 16px;
 box-sizing: border-box; /* Include padding and border in width calculation */
}


.homeformcontainer input::placeholder {
  color: black;
}

.homeformcontainer button {
  background-color: #4de9e9;
  color: #4a1243;
  font-size: 18px;
  outline: none;
  border: 1px solid black;
  border-radius: 12px;
  cursor: pointer;
  padding: 10px;
  width: 40%;
  margin-top: 20px;
}